body {
	background: $background-full;
	background: url('/assets/img/main.png') repeat;
	overflow: hidden;
	@include montserrat();
}

.cover {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	@include flex-center-all(column);
	background: linear-gradient(to bottom, transparent 60%, $black 100%);
	color: $white;
	text-shadow: 0px 0px 5px $black;
}
.container-fluid {
	width: 80%;
}
